body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pics {
    display: inline-block;
    width: 25%;
    margin-right: auto;
    margin-left: auto;
    
    
}

.img-fluid{
    height: 40%;
    width: 80%;
    padding-bottom: 10px;

}

.img-fluid:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.pic-div, .pic-row {
  margin-left: auto;
  margin-right: auto;
  width: 60%;
  height: 60%
}

.topbar {
  background-color: aquamarine;
  height: 60px;
  position: fixed;
  width: 100%;
  text-align: center;
  padding: 10px;
  
}

.jumbotron {
  text-align: center;
  background-image: url("https://i.pinimg.com/originals/6f/08/d6/6f08d6db91d5675e04a851d30f85e213.png");

  
}

.botbar {
  height: 40px;
  background-color: aquamarine;
}

.jumbo-inside {
  background-color: rgba(238,224,224, 0);
}
.badge {
  float: right;
  font-size: 20px;
}

.title-middle {
  float: left;
  font-size: 30px;
}
